___CSS_LOADER_EXPORT___.push([module.id, `body {
  background: #fffff8;
  margin: 30px;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif; }

#app {
  display: grid;
  grid-auto-rows: minmax(150px, auto);
  background: white;
  color: black;
  justify-content: center;
  grid-gap: 1em;
  font-size: 20px;
  font-family: 'Oswald', sans-serif;
  border: none; }

.holder {
  display: inline-block;
  padding: 5px 5px;
  font-size: 20px;
  text-align: justify;
  text-decoration: none;
  outline: none;
  color: black;
  background-color: lightgray;
  border: none;
  border-radius: 10px; }

.headline {
  font-size: 60px;
  color: black;
  text-align: center; }

.entry {
  background: darkblue;
  color: white; }

.title {
  font-size: 35px; }

/*  Basic Styling To Override Default For Basic HTML Elements */
label {
  display: block;
  font-size: 27px; }

input {
  background: lightgray;
  font-size: 20px;
  font-family: 'Oswald', sans-serif;
  border: none; }

h1 {
  font-size: 60px; }

textarea {
  background: lightgray;
  font-size: 20px;
  font-family: 'Oswald', sans-serif;
  border: none;
  /*grid-row: 1;*/ }

input:focus {
  background-color: grey;
  border: none;
  outline: none; }

select:focus {
  background-color: grey;
  border: none;
  outline: none; }

textarea:focus {
  background-color: grey;
  border: none;
  outline: none; }

button:focus {
  outline: none; }

#generate {
  display: inline-block;
  padding: 5px 5px;
  font-size: 40px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: white;
  background-color: darkblue;
  border: none;
  border-radius: 10px;
  box-shadow: 0 9px #999;
  width: 200px;
  height: 100px;
  font-family: 'Oswald', sans-serif; }

#generate:hover {
  background-color: blue; }

#generate:active {
  background-color: navy;
  transform: translateY(4px); }

/* Style for Placeholder Text*/
::placeholder {
  /* Firefox, Chrome, Opera */
  color: navy;
  font-family: 'Oswald', sans-serif;
  border: none; }

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: navy;
  font-family: 'Oswald', sans-serif;
  border: none; }

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: navy;
  font-family: 'Oswald', sans-serif;
  border: none; }
